.BackupButton {
  margin:0 auto;
  border: none;
  color: white;
  border-radius: 4px;
  opacity: 1;
  transition: 0.3s;
  padding-left: 0;
  padding-right: 0;
  color: #fff;
  background: linear-gradient(-45deg, #EE7752, #E73C7E, #23A6D5, #23D5AB);
  background-size: 400% 400%;
  -webkit-animation: Gradient 19s ease infinite;
  animation: Gradient 19s ease infinite;
}

.paused {
  -webkit-animation-play-state: paused; /* Safari 4.0 - 8.0 */
  animation-play-state: paused;
}

@-webkit-keyframes Gradient {
  0% {
  background-position: 0% 50%
}
50% {
background-position: 100% 50%
}
100% {
background-position: 0% 50%
}
}

@keyframes Gradient {
  0% {
  background-position: 0% 50%
  }
50% {
background-position: 100% 50%
}
100% {
background-position: 0% 50%
}
}

@media only screen and (orientation: landscape) {
  .BackupButton {
    width: 60%;
    height: 4.5vw;
    font-size: 2vw;
    margin-top: 3%;
    margin-bottom: 3%;

  }
}

@media only screen and (orientation: portrait) {
  .BackupButton {
    width: 100%;
    height: 8vh;
    margin-top: 10%;
    margin-bottom: 7%;
    font-size: 3.5vh;
    
  }
}

.BackupButton:disabled {opacity: 1}

.BackupButton:hover:enabled {opacity: .7}

.accordion {
    text-align: left;
    border: 0px solid rgba(134, 134, 134, 0.733);
    border-radius: 4px;
    margin-top: 2%;
    width: 20%;
}   

@media only screen and (orientation: landscape) {
  .accordion {
    width: 100%;
    font-size: 1.2vw;
    display: flex;
    flex-direction: column;
    
  }
}

@media only screen and (orientation: portrait) {
  .accordion {
    width: 100%;
    font-size: 1.2vh;
    display: flex;
    flex-direction: column;
  }
}


.accordion__item + .accordion__item {
    border-top: 3px solid rgb(255, 255, 255);
    border-radius: 2px;
}

.accordion__button {
    background-color: rgb(46, 43, 43);
    color: #f4f4f4;
    cursor: pointer;
    padding: 10px;
    text-align: left;
    border: none;
    transition: 0.3s;
    opacity: 1; 
}

.accordion__button:hover {
    background-color: #758796;
}

.accordion__button:before {
    display: inline-block;
    content: '';
    height: 1vh;
    width: 1vh;
    margin-right: 12px;
    border-bottom: 1px solid currentColor;
    border-right: 1px solid currentColor;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

@media only screen and (orientation: landscape) {
  .accordion__button:before {
    height: 1vw;
    width: 1vw;
  }
}

@media only screen and (orientation: portrait) {
  .accordion__button:before {
    height: 1vh;
    width: 1vh;
  }
}

.accordion__button[aria-expanded='true']::before,
.accordion__button[aria-selected='true']::before {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}

[hidden] {
    display: none;
}

.accordion__panel {
    padding: 20px;
    -webkit-animation: fadein 0.35s ease-in;
            animation: fadein 0.35s ease-in;
    font-size: 1.7vh;
}

code {
  color: rgb(0, 174, 233);
}

a {
  color: rgb(0, 174, 233);
}

/* -------------------------------------------------- */
/* ---------------- Animation part ------------------ */
/* -------------------------------------------------- */

@-webkit-keyframes fadein {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@keyframes fadein {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

